const BASE_PATH = "https://api.themoviedb.org/3";
const API_KEY = process.env.REACT_APP_API_KEY;

export interface ITv {
  id: number;
  name: string;
  backdrop_path: string;
  overview: string;
  vote_average: string
}

export interface IGetTvResult {
  page: number;
  results: ITv[];
  total_pages: number;
  total_results: number;
}

export function getTvPopular() {
  return fetch(`${BASE_PATH}/tv/popular?api_key=${API_KEY}&language=en-US`).then(
    (response) => response.json()
  );
}

export function getTvAiringToday() {
  return fetch(`${BASE_PATH}/tv/airing_today?api_key=${API_KEY}&language=en-US`).then(
    (response) => response.json()
  );
}

export function getTvTopRated() {
  return fetch(`${BASE_PATH}/tv/top_rated?api_key=${API_KEY}&language=en-US`).then(
    (response) => response.json()
  );
}

export function getTvSearch(keyword: string) {
  return fetch(`${BASE_PATH}/search/tv?api_key=${API_KEY}&query=${keyword}`).then(
    (response) => response.json()
  );
}