import { useEffect, useState } from "react";
import { useQueries } from "react-query";
import { useLocation } from "react-router";
import styled from "styled-components";
import MovieSlider from "../Components/MovieSlider";
import TvSlider from "../Components/TvSlider";
import { getMovieSearch } from "../Query/Movie";
import { getTvSearch } from "../Query/Tv";

const Wrapper = styled.div`
  background: black;
`;

const Loader = styled.div`
  height: 20vh;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const SerchResult = styled.div`
  position: relative;
  top: 150px;
  padding-bottom: 150px;
  margin-bottom: 80px;
  font-size: 30px;
  margin-left: 20px;
`

const Title = styled.div`
  position: relative;;
  font-size: 25px;
  margin-bottom: 20px; ;
  font-weight: bolder;
`;

const Keyword = styled.div`
  padding-top: 130px;
  /* position: relative; */
  font-size: 30px;
  margin-left: 30px;
  font-weight: bolder;
`;
const ZeroMatch = styled.div`
  font-size: 20px;
  margin-left: 20px;
  margin-bottom: 50px; 
`;

function Search() {
  const location = useLocation();
  const [isLoading, setIsLoading] = useState(true);

  const keyword = new URLSearchParams(location.search).get("query") || "";
  const result = useQueries([
    {
      queryKey: ["movieSearch", keyword],
      queryFn: () => getMovieSearch(keyword)
    },
    {
      queryKey: ["tvSearch", keyword],
      queryFn: () => getTvSearch(keyword)
    }
  ]);
  useEffect(() => {
    const loadingFinishAll = result.some(result => result.isLoading);
    setIsLoading(loadingFinishAll);
  }, [result]);

  console.log(result);
  return (
    <Wrapper>
      {isLoading ? (
        <Loader>Loading...</Loader>
      ) : (
        <>
          <Keyword>Keyword : {keyword}</Keyword>
          <SerchResult>
            { result[0].data.total_results !== 0 
              ? <MovieSlider results={result[0].data.results} sliderTitle={"Movie Search Result"} /> 
              : <><Title>Movie Search Result</Title><ZeroMatch>0 Match</ZeroMatch></>}
            { result[1].data.total_results !== 0 
              ? <TvSlider results={result[1].data.results} sliderTitle={"TV Search Result"} isSearch={true} /> 
              : <><Title>TV Search Result</Title><ZeroMatch>0 Match</ZeroMatch></>}
          </SerchResult>
        </>
      )}
    </Wrapper>
  );
}
export default Search;